html {
  box-sizing: border-box;
  font-size: 62.5%; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-size: 1.3rem;
  font-family: "Montserrat", sans-serif; }

.container {
  width: 95%;
  margin: 0 auto;
  max-width: 120rem; }
  @media (min-width: 1200px) {
    .container {
      width: 60%; } }

a {
  font-style: none; }

img {
  width: 100%; }

h1, h2, h3, h4, h5 {
  margin: 0; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.5rem; }

h3 {
  font-size: 3rem; }

h4 {
  font-size: 2.5rem; }

h5 {
  font-size: 1.8rem; }

span {
  color: #F2994A;
  font-size: 1.7rem;
  font-weight: 700;
  margin-right: 1rem; }

.title {
  padding: 3rem 0; }
  @media (min-width: 768px) {
    .title h4 {
      font-size: 4rem; } }

.subtitle {
  padding: 3rem 0; }
  @media (min-width: 768px) {
    .subtitle {
      padding: 0 0 3rem 0; }
      .subtitle h5 {
        font-size: 3; } }

@media (min-width: 1200px) {
  .productos-userInformation {
    display: grid;
    grid-template-columns: repeat(2, 1fr); }
    .productos-userInformation .productos {
      grid-column: 2 / 3; }
    .productos-userInformation .informationUser {
      grid-row: 1 / 2; } }

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }

/* Firefox */
.producto-cantidad input[type=number] {
  -moz-appearance: textfield;
  border: none;
  width: 4rem;
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  color: #4E5150;
  margin: 0 1.66667rem;
  background-color: transparent; }

input:focus {
  outline: none; }

.campo {
  color: #828282;
  display: flex;
  flex-direction: column;
  padding-bottom: 2.5rem; }
  .campo label {
    margin-bottom: 1rem; }
  .campo input, .campo select {
    padding: 1rem;
    border-radius: 1rem;
    font-weight: 700;
    border: solid .2rem #828282;
    background-color: #fff; }

.campo .errorCampo {
  border-color: red; }

.twoColumns {
  display: grid;
  grid-template-columns: repeat(2, 1fr); }
  .twoColumns input, .twoColumns select {
    width: 12.5rem;
    -moz-appearance: textfield; }
    @media (min-width: 1200px) {
      .twoColumns input, .twoColumns select {
        width: 22.5rem; } }

.derecha {
  display: flex;
  justify-content: end; }

.btn {
  background-color: #F2994A;
  border: solid 4px #F2994A;
  border-radius: 1rem;
  color: #fff;
  font-weight: 700;
  font-size: 1.8rem;
  padding: 2rem 4rem;
  cursor: pointer;
  margin: 5rem 0; }

.btn:hover {
  background-color: #fff;
  color: #F2994A;
  -webkit-box-shadow: 1px 1px 13px 0px rgba(50, 50, 50, 0.42);
  -moz-box-shadow: 1px 1px 13px 0px rgba(50, 50, 50, 0.42);
  box-shadow: 1px 1px 13px 0px rgba(50, 50, 50, 0.42); }

.createdBy p {
  text-align: center;
  color: #828282; }

.createdBy span {
  color: #828282;
  font-size: 1.3rem;
  margin: 0; }

.productos {
  background-color: #F2F2F2;
  height: auto;
  border-radius: 2rem;
  --webkit-box-shadow: 2px 2px 6px 0px rgba(50, 50, 50, 0.25);
  -moz-box-shadow: 2px 2px 6px 0px rgba(50, 50, 50, 0.25);
  box-shadow: 2px 2px 6px 0px rgba(50, 50, 50, 0.25); }
  @media (min-width: 1200px) {
    .productos {
      height: 72rem; } }

.producto {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem; }
  .producto img {
    border-radius: 1rem; }

.picture {
  margin-right: 2rem; }

.producto-cantidad {
  border: solid 2px #8282;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1.5rem;
  width: 100%;
  height: 5rem;
  padding: .5rem; }

.btn-cantidad {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #E0E0E0;
  color: #828282;
  border: none;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  border-radius: 1rem;
  font-weight: 700;
  font-size: 2rem; }

.shipping-total {
  margin-top: 1.66667rem;
  padding: 0 1rem;
  font-weight: 700;
  font-size: 1.5rem; }

.shipping, .total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: solid 1px #bdbdbd; }

#email::placeholder, #phone::placeholder {
  background-repeat: no-repeat;
  background-position: .5rem center;
  background-size: 15px 15px; }

#phone::placeholder {
  background-image: url(../img/contactIcons/phone.png); }

#email::placeholder {
  background-image: url(../img/contactIcons/email.png); }

#fullName::placeholder, #address::placeholder, #city::placeholder, #country::placeholder, #postalcode::placeholder {
  background-repeat: no-repeat;
  background-position: .5rem center;
  background-size: 15px 15px; }

#fullName::placeholder {
  background-image: url(../img/shippingIcon/user.png); }

#address::placeholder {
  background-image: url(../img/shippingIcon/address.png); }

#city::placeholder {
  background-image: url(../img/shippingIcon/city.png); }

#postalcode::placeholder {
  background-image: url(../img/shippingIcon/postalCode.png); }
